import "./styles.css";
import { v4 as uuidv4 } from "uuid";
import { useState, useEffect,Fragment } from "react";
import {
  TextField,
  Button,
  Paper,
  Typography,
  Fab,
  Toolbar,
  AppBar,
  Grid,
  IconButton,
  Snackbar
} from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";
import EditOutlinedIcon from "@material-ui/icons/EditOutlined";
import DoneOutlinedIcon from "@material-ui/icons/DoneOutlined";
import AddOutlinedIcon from "@material-ui/icons/AddOutlined";
import InfoOutlinedIcon from "@material-ui/icons/InfoOutlined";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { useTheme } from "@material-ui/core/styles";
import { makeStyles } from "@material-ui/core/styles";
import CloseIcon from '@material-ui/icons/Close';

const useStyles = makeStyles((theme) => ({
  snackbar: {
    [theme.breakpoints.down("xs")]: {
      bottom: 90,
    },
  },
}));

export default function App() {
  const classes = useStyles();
  const [list, setlist] = useState([]);
  const [text, settext] = useState("");
  const [edittext, setedittext] = useState("");
  const [editindex, setindex] = useState(null);
  const [showInfo, setShowInfo] = useState(false);
  const [loaded, setloaded] = useState(false);
  const [open, setOpen] = useState(false);
  const [snackMessage, setsnackMessage] = useState("");


  const genuuid = () => {
    while (true) {
      var uuid_ = uuidv4();
      var formattedls = list.filter((item) => item.key === uuid_);
      if (formattedls.length === 0) return uuid_;
    }
  };
  const delitem = (key) => {
    setlist((ls) => ls.filter((item) => item.key !== key));
    setsnackMessage("task successfully deleted")
  };
  const additem = () => {
    if (text) {
      handleClose();
      setlist((ls) => ls.concat({ key: genuuid(), text: text }));
      settext("");
      setsnackMessage("task successfully added")
    }
  };
  const CloseSnack = (event, reason)=>{
    if (reason === 'clickaway') {
      return;
    }
    setsnackMessage("");
}
  const handleClose = () => {
    setOpen(false);
    setShowInfo(false);
  };
  const edititem = (key, text) => {
    const listdup = list;
    for (let i in listdup) {
      if (listdup[i].key === key) listdup[i].text = text;
    }

    if (localStorage) localStorage.setItem("todo", JSON.stringify(listdup));
    setlist(listdup);
  };
  useEffect(() => {
    if (localStorage.todo) setlist(JSON.parse(localStorage.todo));
    setloaded((ld) => !ld);
  }, []);
  useEffect(() => {
    if (localStorage) localStorage.setItem("todo", JSON.stringify(list));
  }, [list]);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));
  return (
    <div className="App">
      <AppBar position="static">
        <Toolbar>
          <Grid container alignItems={"center"} justify={"space-between"}>
            <Typography variant="h6">Todo</Typography>
            <IconButton
              aria-label="app info"
              style={{ color: "white" }}
              onClick={() => setShowInfo(true)}
            >
              <InfoOutlinedIcon />
            </IconButton>
          </Grid>
        </Toolbar>
      </AppBar>
      {/* dialog for todo info */}
      <Dialog
        fullScreen={fullScreen}
        open={showInfo}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">about this app</DialogTitle>
        <DialogContent>
          <DialogContentText>
            You can add the tasks that you have planned for your day in this
            application so you would remember them. <br />
            this app is actually a PWA, which means you can install it on your
            local machine and use it even when you don't have an internet
            connection.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            dismiss
          </Button>
        </DialogActions>
      </Dialog>
      {/* dialog for adding to do */}
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">add to-do</DialogTitle>
        <DialogContent>
          <DialogContentText>
            You got a task that you should do? <br />
            Write it down in the box and click the add button to add it to the
            list.
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="taskTD"
            label="task to do"
            fullWidth
            value={text}
            onChange={(e) => settext(e.target.value)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={additem} color="primary">
            add
          </Button>
        </DialogActions>
      </Dialog>
      {list.length !== 0 ? (
        list.map((item, index) => {
          return (
            <Paper className="todo">
              <div className="info">
                {!(editindex === index) ? (
                  <Typography>{item.text}</Typography>
                ) : (
                  <TextField
                    value={edittext}
                    onChange={(e) => setedittext(e.target.value)}
                    style={{ width: "95%" }}
                  />
                )}
              </div>
              <div className="control">
                {editindex === index ? (
                  <DoneOutlinedIcon
                    onClick={() => {
                      setindex(null);
                      setedittext("");
                      edititem(item.key, edittext);
                    }}
                    style={{ cursor: "pointer" }}
                  />
                ) : (
                  <EditOutlinedIcon
                    onClick={() => {
                      setindex(index);
                      setedittext(item.text);
                    }}
                    style={{ cursor: "pointer" }}
                  />
                )}
                <DeleteOutlineIcon
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    delitem(item.key);
                  }}
                />
              </div>
            </Paper>
          );
        })
      ) : (
        <Alert style={{ margin: ".5rem" }} severity="info">
          your tasks will be shown here!
        </Alert>
      )}
      {!open ? (
        <Fab
          color={"primary"}
          className="fab"
          style={{ position: "absolute" }}
          aria-label="add todo"
          onClick={() => {
            setOpen(true);
          }}
        >
          <AddOutlinedIcon />
        </Fab>
      ) : null}

      <Snackbar
                className={classes.snackbar}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        open={snackMessage}
        autoHideDuration={3000}
        onClose={CloseSnack}
        message={snackMessage}
        action={
          <Fragment>
            <IconButton size="small" aria-label="close" color="inherit" onClick={CloseSnack}>
              <CloseIcon fontSize="small" />
            </IconButton>
          </Fragment>
        }
      />
    </div>
  );
}
